* {
  font-family: 'Raleway', sans-serif;
  box-sizing: border-box;

  --main-color: rgb(15, 22, 40);
  --main-color-active: rgb(14, 19, 36);
  --bg-1: rgb(26, 32, 54);
  --bg-2: rgb(30, 38, 65);
  --bg-3: rgb(48, 56, 90);
  --bg-4: rgb(19, 25, 44);
  --bg-5: rgb(58, 67, 106);
  --bg-6: rgb(40, 49, 78);
  --white: white;
  --black: black;
  --forest-green: rgb(2, 15, 13);
  --lime-green: rgb(159, 209, 159);
  --faded-white: rgba(255, 255, 255, 0.05);
  --faded-white-hover: rgba(255, 255, 255, 0.15);
  --faded-white-2: rgba(255, 255, 255, 0.25);
  --faded-white-3: rgba(255, 255, 255, 0.6);
  --faded-black: rgba(0, 0, 0, 0.6);
  --white-active: rgb(147, 147, 147);
  --accent-magenta: rgb(171, 110, 179);
  --scrollbar: rgba(189, 189, 189, 0.4);
  --scrollbar-hover: rgba(179, 179, 179, 0.6);
  --scrollbar-track: rgb(16, 44, 59);
  --gradient-1: linear-gradient(45deg, rgb(18, 194, 233), rgb(196, 113, 237), rgb(246, 79, 89));
  --gradient-2: linear-gradient(rgb(253, 200, 48), rgb(243, 115, 53));

  --contact-padding: 15px 20px;
  --bg-skew-angle: -5deg;
  --fade-in: cubic-bezier(.17, 1.07, .72, .9);
}

body {
  background-color: var(--bg-1);
}

img {  
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

::-webkit-scrollbar {
  background-color: transparent;
  z-index: 10;
}

::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
  z-index: 10;

}

::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar);
  z-index: 10;

}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--scrollbar-hover);
}

::placeholder {
  color: var(--white);
  opacity: 0.75;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: var(--white);
  -webkit-box-shadow: 0px 0px 0px 100px var(--bg-1) inset;
  transition: background-color 500ms ease-in-out 0s;
}

::selection {
  background: #2c508e;
}

::-moz-selection {
  background: #2c508e;
}

@keyframes HeaderFade {
  0% {
      opacity: 0;
      transform: scaleX(0);
  }

  100% {
      transform: scaleX(1);
      opacity: 1;
  }
}